import React from 'react';
import './AboutUsHome.scss';
import data from '../../Data/Data.json';

import aboutUsImg from '../../Images/AboutUs-home/aboutUs.jpg';



const AboutUsHome = () => {


    return (
        <div className="aboutUsHome padding--8-0">
            <div className="container">
                <div className="row">
                    <div className="col-md-6">
                        <div className="title__sm-title">
                            <h4 className="text-uppercase mb-3">{data.aboutUsHome.smTitle}</h4>
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="title__lg-title">
                            <h2>{data.aboutUsHome.lgTitle}</h2>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-6 col-md-3 aboutUsHome__stats">
                        <p>{data.aboutUsHome.numEmployees}</p>
                        <p>{data.aboutUsHome.descEmployees}</p>
                    </div>
                    <div className="col-6 col-md-3 aboutUsHome__stats">
                        <p>{data.aboutUsHome.numExperience}</p>
                        <p>{data.aboutUsHome.descExperience}</p>
                    </div>
                    <div className="col-6 col-md-3 aboutUsHome__stats">
                        <p>{data.aboutUsHome.numContracts}</p>
                        <p>{data.aboutUsHome.descContracts}</p>
                    </div>
                    <div className="col-6 col-md-3 aboutUsHome__stats">
                        <p>{data.aboutUsHome.numInvestments}</p>
                        <p>{data.aboutUsHome.descInvestments}</p>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-6">
                        <img className="w-100" src={aboutUsImg} alt="About us" />
                    </div>
                    <div className="col-md-6">
                        <p className="aboutUsHome__desc">{data.aboutUsHome.desc}</p>
                        <a href="/" className="button" role="button">{data.aboutUsHome.smTitle}</a>
                    </div>
                </div>
            </div>
            
        </div>
    )
}

export default AboutUsHome;