import React from 'react';
import './Nav.scss';
import logo from '../../Images/Logo/logo.svg';
import data from '../../Data/Data.json';



const Nav = () => {


    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-transparent navbar__nav">
            <a className="navbar-brand navbar__brand" href="/">
                <img src={logo} alt="Construction Inc. logo" />
            </a>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>

            <div className="phone">
                <a href = {"tel:" + data.phone}>{data.phone}</a>
            </div>

            <div className="collapse navbar-collapse justify-content-end" id="navbarSupportedContent">
                <ul className="navbar-nav navbar__links">
                    <li className="nav-item active">
                        <a className="nav-link text-white" href="/">About us <span className="sr-only">(current)</span></a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link text-white" href="/">Our services</a>
                    </li>

                    <li className="nav-item">
                        <a className="nav-link text-white" href="/">How we work?</a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link text-white" href="/">Portfolio</a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link text-white" href="/">Contact us</a>
                    </li>
                </ul>
                
            </div>
        </nav>
    )
}

export default Nav;