import React from 'react';
import './ServicesHome.scss';
import data from '../../Data/Data.json';





const ServicesHome = () => {


    return (
        <div className="servicesHome padding--8-0 pb-0">
            <div className="container">
                <div className="row">
                    <div className="order-2 order-md-1 col-md-6">
                        <div className="title__lg-title">
                            <h2>{data.servicesHome.lgTitle}</h2>
                        </div>
                    </div>
                    <div className="order-1 order-md-2 col-md-6">
                        <div className="title__sm-title">
                            <h4 className="text-uppercase mb-3">{data.servicesHome.smTitle}</h4>
                        </div>
                    </div>
                </div>
                <div className="row pb-5">
                    <div className="col-md-8">
                        <p className="pt-5">{data.servicesHome.desc}</p>
                    </div>
                </div>
                
                <div className="servicesHome__serviceBox">
                    <div className="row">
                        <div className="col-md-4">
                            <h4>{data.servicesHome.serviceTitle.construction}</h4>
                            <p>{data.servicesHome.serviceDesc.constructionDesc}</p>
                        </div>
                        <div className="col-md-4">
                            <h4>{data.servicesHome.serviceTitle.finising}</h4>
                            <p>{data.servicesHome.serviceDesc.finishingDesc}</p>
                        </div>
                        <div className="col-md-4">
                            <h4>{data.servicesHome.serviceTitle.instalation}</h4>
                            <p>{data.servicesHome.serviceDesc.instalationDesc}</p>
                        </div>
                    </div>
                    <a href="/" className="button" role="button">{data.servicesHome.smTitle}</a>
                </div>
            </div>
        </div>
    )
}

export default ServicesHome;