import React from 'react';
import './Hero.scss';
import HeroImg from '../../Images/Hero/hero.jpg';
import Nav from '../Nav/Nav';
import data from '../../Data/Data.json';



const Hero = () => {


    return (
        <div className="hero" style={{backgroundImage: `url(${HeroImg})`}}>
            <Nav />
            <div className="hero__content">
                <h1 className="hero__title">{data.hero.title}</h1>
                <p className="hero__desc">{data.hero.desc}</p>
                <a className="button" href="/" role="button">{data.hero.btnText}</a>
            </div>
        </div>
    )
}

export default Hero;