import React from 'react';
import './PortfolioHome.scss';
import data from '../../Data/Data.json';

import img01 from '../../Images/portfolio/img-1.jpg';
import img02 from '../../Images/portfolio/img-2.jpg';
import img03 from '../../Images/portfolio/img-3.jpg';
import img04 from '../../Images/portfolio/img-4.jpg';



const PortfolioHome = () => {


    return (
        <div className="container-fluid portfolioHome pb-0">
            <div className="row">
                

                    <div className="col-md-3 offset-md-2">
                        <div className="row">
                            <div className="portfolioHome__content-left">
                                <div className="title__sm-title">
                                    <h4>{data.portfolioHome.smTitle}</h4>
                                </div>
                                <div className="title__lg-title">
                                    <h2>{data.portfolioHome.lgTitle}</h2>
                                </div>
                                <div className="portfolioHome__desc">
                                    <p>{data.portfolioHome.desc}</p>
                                </div>
                                <a className="button" href="/" role="button">{data.portfolioHome.smTitle}</a>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-7">
                        <div className="row max-height">
                            <div id="carouselExampleFade" className="carousel slide carousel-fade" data-ride="carousel">
                                <div className="carousel-inner">
                                    <div className="carousel-item active">
                                        <img src={img01} className="d-block w-100" alt="..." />
                                    </div>
                                    <div className="carousel-item">
                                        <img src={img02} className="d-block w-100" alt="..." />
                                    </div>
                                    <div className="carousel-item">
                                        <img src={img03} className="d-block w-100" alt="..." />
                                    </div>
                                    <div className="carousel-item">
                                        <img src={img04} className="d-block w-100" alt="..." />
                                    </div>
                                </div>
                            </div>
                            <a className="carousel-prev" href="#carouselExampleFade" role="button" data-slide="prev">
                                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                <span className="sr-only">Previous</span>
                            </a>
                            <a className="carousel-next" href="#carouselExampleFade" role="button" data-slide="next">
                                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                <span className="sr-only">Next</span>
                            </a>
                        </div>
                        
                    </div>
                   
               
            </div>
        </div>
        
        
    )
}

export default PortfolioHome;