import React from 'react';
import './Footer.scss';
import data from '../../Data/Data.json';


const Footer = () => {


    return (
        <div className="container-fluid footer padding--8-0">
            <div className="container">
                <div className="row">
                    <div className="col-md-6 footer__content">
                        <p>{data.footer.contact}</p>
                        <p className="c-orange">{data.footer.contactLinkText}</p>
                        <div className="phone">
                            <a href = {"tel:" + data.phone}>{data.phone}</a>
                        </div>
                        <p>{data.footer.address}</p>
                    </div>
                    <div className="col-md-6 footer__content">
                        <p>{data.footer.jobQuote}</p>
                        <p className="c-orange">{data.footer.jobQuoteText}</p>
                        <a className="button" href={"mailto:" + data.email} role="button">{data.footer.jobQuoteBtnText}</a>
                        <p>{data.footer.JQdisclamer}</p>
                    </div>
                </div>
                <hr />
                <p>&copy; {data.footer.copyright}</p>
            </div>
        </div>
        
        
    )
}

export default Footer;